body {
  scroll-behavior: smooth;
  -webkit-font-smoothings: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  position: relative;
  min-height: 100vh;
}

.content {
  margin-top: 2rem;
  margin-bottom: 10rem;
  max-width: 53rem;
  width: 100%;
}

/* Default behavior is for mobile */

.content-wrapper {
  display: flex;
  flex-wrap: wrap;
}

/* Alternate behavior is for wider screens  */

@include media-breakpoint-up(md) {
  /* medium 	768px */

  .content-wrapper {
    flex-wrap: nowrap;
    justify-content: space-between;
    .guide-contents-nav {
      /* In case it was toggled closed  */
      display: block !important;
    }
  }
  #content {
    margin-left: $container-padding-x;
    margin-right: $container-padding-x;
  }
  .content {
    padding: 0;
  }
}
