.footer {
  background-color: $primary;
  .footer-text {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    div + div {
      span::before {
        content: "";
        width: 1.1rem;
        display: inline-block;
        text-align: left;
      }
    }
  }
  a:link,
  a:active,
  a:visited {
    color: $white;
    opacity: 0.8;
    text-decoration: none;
    padding: 0.75em 0 0.75em 0.75em;
    display: block;
    font-size: .9rem;
  }
  a:hover {
    color: $white;
    opacity: 1;
    text-decoration: underline;
  }
  svg {
    vertical-align: text-top;
  }
}
