.header-collection {
  > div {
    
    padding: ($spacer * 0) 0 ($spacer * 1.5);
    /* if there's a floater */
    overflow: auto;
    @include media-breakpoint-up(md) {
      border-bottom: 6px solid $hr-color;
    }
  }
}
.collection-title-siteheader {
  @extend .display-1;
  a {
    text-decoration: none;
    @include font-size(2.1rem);
    line-height: 1.3;
    color: $primary;
    .ead-num {
      color: var(--bs-dark);
      font-size: smaller;
      font-weight: 200;
    }
  }
}