* {
  @extend .text-break;
}

.content a:link {
  @include content-link();
}

.page-title,
#content > h1,
.recursions0 > h1 {
  @extend .display-1;
}

.controlaccess a,
.donors a {
  &:hover {
    @include content-link-hover();
  }
}
a:link .image-title {
  @include content-link();
}
a:hover .image-title {
  @include content-link-hover();
}
.dao-item a:hover {
  @include content-link-hover();
}
.subtitle {
  @extend .display-3;
}

.page-title ~ .subtitle {
  margin-top: -0.8rem;
  margin-bottom: 1.5rem;
}
code {
  padding: 2px 4px;
  font-size: 90%;
  background-color: #eeeeee;
}

pre {
  display: block;
  padding: 9.5px;
  margin: 0 0 10px;
  line-height: 1.4;
  word-break: break-all;
  word-wrap: break-word;
  border: 1px solid #ccc;
}

pre,
code {
  font-family: monocode, monospace;
  -webkit-font-smoothings: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

emph {
  font-style: italic;
}
title {
  display: inline;
}

// .item-title {
//   font-weight: bold;
//   font-size: 1.1rem;
// }
.formattednote-header {
  text-transform: uppercase;
  font-size: 1rem;
  margin: 20px 0 10px;
}
.formattednote-subheader {
  text-transform: uppercase;
  font-size: 0.85rem;
  margin: 0 0 20px;
}

.controlaccess-inner .sub-label {
  font-size: 0.8rem;
  text-transform: uppercase;
  align-self: baseline;
  padding-right: 1em;
  flex: 0 0 auto;
}
.sub-label {
  font-size: 0.9em;
  text-transform: uppercase;
  display: inline;
  margin-bottom: inherit;
}
.sub-label::after {
  content: ":";
}

.collection-title-siteheader {
  a {
    text-decoration: none;
  }
}
ul ul a {
  // font-size: 0.9em !important;
  padding-left: 30px !important;
  /* background: #6d7fcc; */
}
.md-group {
  margin: $spacer/2 0;
  br {
    //display: none;
  }
  div {
    margin-bottom: unset;
  }
}
.ead-container .formattednote-header {
  text-transform: capitalize;
  margin: 0 .1em 0 0 ;
  display: inline-block;
  font-weight: inherit;
  font-size: inherit;
}
.level-file .md-group {
  margin: 0;
}
.extent-group {
  margin-bottom: 0;
}
.unit_id,
.unit_date,
.langmaterial {
  &.md-group {
    //  background-color: fuchsia;
    vertical-align: top;
    .formattednote-header {
      //background-color: yellow;
      display: inline-block;
      margin: 0 1rem 0 0;
    }
    > div {
      //background-color: red;
      display: inline-block;
      vertical-align: top;
      margin-bottom: 0;
      p:last-child {
        margin-bottom: 0;
      }
    }
  }
}

