.skip-nav a {
  position: absolute;
  left: -10000px;
  top: 0;
  width: 1px;
  height: auto;
  overflow: hidden;
  &:focus {
    position: absolute;
    top: 5px;
    left: 15px;
    width: auto;
    height: auto;
    z-index: 999999;
    background-color: white;
    padding: 5px 10px;
    overflow: visible;
  }
}
