.image-gallery-item {
  padding-top: $spacer;
  margin-top: $spacer;
  img,
  a:link {
    display: block;
  }
  .dao-image {
    margin-bottom: ($spacer / 2);
    box-shadow: 2px 2px 2px #5353537c;
    overflow: hidden;
  }

  @include make-col-ready();

  @include make-col(12);
  @include media-breakpoint-up(sm) {
    @include make-col(6);
  }
  @include media-breakpoint-up(lg) {
    @include make-col(4);
  }
  @include media-breakpoint-up(xl) {
    @include make-col(3);
  }
}
.image-service {
  img {
    width: 100%;
    max-width: 100%;
    height: auto;
    transition: all 0.1s ease-in-out;
  }
  a {
    display: flex;
    flex-wrap: nowrap;
  }
  a:hover {
    img {
      transform: scale(1.05);
      opacity: 0.9;
    }
  }
}
.dao-item {
  .thumbnail-hold {
    display: inline-block;
    width: 125px;
    height: auto;
    margin-right: $spacer;
    overflow: hidden;
    box-shadow: 2px 2px 2px #5353537c;
    img {
      width: 100%;
      min-width: 100%;
      height: auto;
      display: inline-block;
      background: #e6e4e4;
    }
  }

  .item-title {
    margin-bottom: 0;
    display: inline-block;
    font-weight: 500;
    font-size: 1rem;
  }
  .image-title {
    margin-bottom: 0;
    display: inline-block;
    font-weight: 500;
  }
  .image-service .item-label {
    line-height: 1;
    font-size: smaller;
    // font-weight: lighter;
    display: inline-block;
  }
}

