.number-of-items {
  font-size: smaller;
  font-weight: lighter;
  white-space: nowrap;
}
.pagination {
  font-size: 0.9rem;
  .page-item.active .page-link {
    background-color: #fefefe;
    color: #000;
    border: 1px solid #9ce9f6;
    box-shadow: 1px 1px 3px #c5c5c5;
  }
  .page-link {
    border-radius: 100%;
    border: none;
    color: $black;
  }
}
