/* ***************** */
/* classname "recursions" is for horizontal indentation */
/* ***************** */
/* ***************** */

.recursions0 {
  ~ .recursions0 {
    /* When there are multiple top-levels on the page - contents page or view all page  */
    &.level-series,
    .level-otherlevel {
      /* Happens on View All page */
      /* We don't want this dramatic treatment for level-file or level-item */
      border-top: 6px solid #4444449a;
      padding-top: 4rem;
      margin-top: 6rem;
    }
  }
}

.recursions1 {
  // border-top: 2px solid black;
}
.recursions2,
.recursions3,
.recursions4,
.recursions5,
.recursions6 {
  margin-left: ($spacer * 3);
}

/* ***************** */
/* Levels */
/* ***************** */
/* ***************** */
.c-items {
  //padding-top: ($spacer * 3);
  //border-top: 6px solid #ddcde8;
}
.level-subseries,
.level-otherlevel:not(.recursions0) {
  margin-top: ($spacer * 2);
  & > .unittitle {
    @extend .display-3;
    padding-top: ($spacer * 3);
    border-top: 6px solid $hr-color;
    margin-top: ($spacer * 3);
  }
}

.level-item {
  border-top: 1px solid $hr-color;
  margin-top: $spacer;
  padding-top: $spacer;
  padding-bottom: $spacer / 2;
  .unittitle {
    font-size: 1.15rem;
    margin-bottom: 0.5rem;
    line-height: 1.3;
  }
}

.level-file {
  border-top: 1px solid $hr-color;
  margin-top: $spacer;
  padding-top: $spacer;
  div {
    margin-bottom: 0;
  }
  .scopecontent {
    p:last-of-type {
      margin-bottom: 0;
    }
    p {
      display: inline-block;
    }
  }
  .unittitle {
    font-size: 1.15rem;
    margin-bottom: 0.5rem;
    line-height: 1.3;
  }
}

.formattednote-header {
  @extend .display-5;
  text-transform: uppercase;
  font-size: 0.9rem;
  margin: 0 $spacer 0.3em 0;
  display: inline-block;
}

.physdesc {
  .formattednote-header {
    margin-top: $spacer;
  }
  .extent-header {
    /* a way to group all the "Extent" items without repeating the header each time */
    display: none;
    &:first-of-type {
      display: block;
    }
  }
}
.origination {
  &.has-non-donor-role-false {
    display: none;
  }
  &.has-non-donor-role-true {
    display: block;
  }
  .role-Donor {
    display: none;
  }
}
