.dao-items {
  margin: 1rem 0 3rem;
  padding-top: 1rem;
  .dao-item {
    &.audio-service,
    &.video-service,
    &.external-link {
      border-bottom: 1px solid $purple-100;
      padding: 0.75rem 0px;
    }
  }
}
.dao-wrap {
  display: flex;
  vertical-align: text-top;
}
.dao-image {
  margin-right: 10px;
  a {
    display: block;
    box-shadow: 1px 1px 2px #5353537c;
  }
}
a.dao-imageset {
  .dao-info {
    padding: 5px 0px;
  }

  display: block;
  .dao-image {
    box-shadow: 1px 1px 0px cyan, 2px 2px 0px #008996, 3px 3px 0px cyan,
      4px 4px 0px #008996;
    box-shadow: 1px 1px 0px #eeeeee, 2px 2px 0px #0a1011dc, 3px 3px 0px #eeeeee,
      4px 4px 0px #0a1011dc;
  }
  img {
    border-width: 1px;
  }
}
a.dao-link.external-link {
  display: inline-block;
}
.dao-item .item-title.role-unknown {
  display: block;
  a.dao {
    display: block;
  }
}

.dao-info {
 // order: 4;
  .view-label {
    font-size: .8rem;
    text-transform: uppercase;
    color: #5e5e5e;
    display: block;
  }
}
.dao-request {
  font-size: 0.9rem;
}
.dao-parenthetical {
  font-size: 0.9rem;
  font-weight: 300;
  white-space: nowrap;
}
// on single DAO pages
.dl-permalink {
  margin: -0.5rem 0 2rem;
}
.next-previous-links {
  display: flex;
  font-size: 0.85rem;
  margin: 3rem 0 1rem;

  a {
    flex: 1 1 50%;
    display: block;
    &.previous-post {
      padding: 5px 2rem 5px 0;
      text-align: left;
    }
    &.next-post {
      padding: 5px 0 5px 2rem;
      text-align: right;
    }
    &:link,
    &:visited {
      color: black;
    }
    &:hover {
      text-decoration: underline;
    }
  }
}
